import React, { useEffect, useState } from 'react'
import HomeCollectionTitle from './HomeCollectionTitle'
import '../styles/homeCollection.scss'
import ListCard from './ListCard'

export default function HomeCollection() {
  const [dataTourTrongNuoc, setDataTourTrongNuoc] = useState([])
  const [dataTourNuocNgoai, setDataTourNuocNgoai] = useState([])

  useEffect(() => {
    // Hm c gi khi component c render hoc state thay i
    fetchDataTourTrongNuoc();
    fetchDataTourNuocNgoai();
  }, []); // Tham s th hai l mt mng rng, ch gi useEffect mt ln sau khi component c mount

  async function fetchDataTourTrongNuoc() {
    try {
      const response = await fetch('https://travel-booking-1.onrender.com/api/tours?catagoryId=1&_limit=6'); // Gi API  ly d liu
      const jsonData = await response.json(); // Chuyn i d liu nhn c thnh JSON
      setDataTourTrongNuoc(jsonData); // Cp nht state vi d liu t API
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  }
  async function fetchDataTourNuocNgoai() {
    try {
      const response = await fetch('https://travel-booking-1.onrender.com/api/tours?catagoryId=2&_limit=6'); // Gi API  ly d liu
      const jsonData = await response.json(); // Chuyn i d liu nhn c thnh JSON
      setDataTourNuocNgoai(jsonData); // Cp nht state vi d liu t API
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  }
  return (
    <div className='container home-collection'>
      <div className='row'>
        <div className='col-12'>
          <HomeCollectionTitle
            title={"im n yu thch trong nc"}
            des={"Ln rng xung bin. Trn vn Vit Nam"}
          />
        </div>
        <div className='col-12'>
          <ListCard data={dataTourTrongNuoc} priceRow={true} />
        </div>
      </div>

      <div className='row'>
        <div className='col-12'>
          <HomeCollectionTitle
            title={"im n yu thch nc ngoi"}
            des={"Bao la th gii. Bn b l nh"}
          />
        </div>
        <div className='col-12'>
          <ListCard
            data={dataTourNuocNgoai} priceRow={true}
          />
        </div>
      </div>
    </div>
  )
}
